<template>
  <div class="wrapper" v-if="users.length > 0 && actualOk">
    <div id="funds">
      <div class="title">{{actualUser.username}}</div>
      <b-tabs fill v-model="tabIndex">
        <b-tab v-if="actualOk">
          <template slot="title">
            <fa :icon="['fas', 'home']"></fa>
          </template>
          <div id="camp">
            <div id="listSeeds" v-if="Object.keys(actualUser.seeds).length > 0">
              <h2>Campanhas participantes</h2>
              <template v-for="sd in actualUser.seeds">
                <b-button v-b-toggle="'seed' + actualUser.id+sd.id" variant="info">
                  <fa :icon="['far', 'file']"></fa>
                  {{sd.description}}
              </b-button>
                <b-collapse :id="'seed' + actualUser.id+sd.id" :accordion="'ac' + actualUser.id">
                  <div class="bts">
                    <!-- <sub @click="tabIndex = 2">Realizar Transferência</sub> -->
                    <!-- <sub @click="tabIndex = 3">Realizar Saque</sub> -->
                    <!-- {{sd.id}}
                    {{fracIdCamp}} -->
                  </div>
                  <div class="results">
                    <div class="valores">
                      <h4>Total do Crédito:</h4>
                      <span>{{parseFloat(sd.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</span>
                      <span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span>
                    </div>
                    <div class="valores">
                      <h4>Disponível:</h4>
                      <span>{{parseFloat(sd.balance).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</span>
                      <span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span>
                    </div>
                  </div>
                  <b-button v-b-toggle="'seed' + actualUser.id+sd.id + '-inner'" variant="info">Ver saldo detalhado</b-button>
                  <b-collapse :id="'seed' + actualUser.id+sd.id + '-inner'">
                    <div class="history" v-for="(hs, index) in sd.history">
                      <template v-if="hs.created">
                        <p>Você criou a campanha <strong>{{sd.description}}</strong> com um valor de <strong>{{parseFloat(hs.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong></p>
                      </template>
                      <template v-else>
                        <!-- <template v-if="(hs.emitterId == hs.ownerId) && !hs.created && hs.sender"> -->
                        <template v-if="index != 0">
                          <template v-if="(sd.history[index-1].status == 'USED' && (sd.history[index-1].emitterId != actualUser.id || sd.history[index-1].emitterId == sd.history[index-1].ownerId)) || (hs.status == 'USED' && index == sd.history.length)">
                            <p>Você sacou <strong>{{parseFloat(sd.history[index-1].balance).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong></p>
                          </template>
                          <template v-else-if="hs.sender">
                            <p>Você transferiu <strong>{{parseFloat(hs.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong> para <strong>{{users.find(us => us.id == hs.ownerId).username}}</strong></p>
                          </template>
                          <template v-else>
                            <p>Você recebeu <strong>{{parseFloat(hs.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong> de <strong>{{users.find(us => us.id == hs.emitterId).username}}</strong></p>
                          </template>
                        </template>
                        <template v-else>
                          <template v-if="hs.sender">
                            <p>Você transferiu <strong>{{parseFloat(hs.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong> para <strong>{{users.find(us => us.id == hs.ownerId).username}}</strong></p>
                          </template>
                          <template v-else>
                            <p>Você recebeu <strong>{{parseFloat(hs.value).toLocaleString('pt-BR', (sd.type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="sd.type == 'PROMOTIONAL'"> Pontos</span></strong> de <strong>{{users.find(us => us.id == hs.emitterId).username}}</strong></p>
                          </template>
                        </template>
                      </template>
                    </div>
                  </b-collapse>
                </b-collapse>
              </template>
            </div>
            <div v-else>
              <h2>Você não possui campanhas</h2>
            </div>
          </div>
        </b-tab>
        <b-tab>
          <template slot="title">
            <fa :icon="['fas', 'plus-circle']"></fa>
          </template>
            <div id="create" v-if="1 == 1">
              <h2>Criar Crédito</h2>
              <form action="">
                <label for="nome">Nome:</label>
                <input name='nome' type="text" v-model="createName" placeholder="Digite aqui o nome do seed">
                <br>
                <label for="tipo">Tipo:</label>
                <input type="radio" v-model="type" id="reais" name="tipo" value="MONETARY">
                <label for="reais">Reais (R$)</label>
                <input type="radio" v-model="type" id="promo" name="tipo" value="PROMOTIONAL">
                <label for="promo">Pontos</label>
                <br>
                <label for="valor">Valor:</label>
                <vue-autonumeric v-model="createVal"
                :options="{
                  digitGroupSeparator: '.',
                  decimalCharacter: ',',
                  decimalCharacterAlternative: '.',
                  currencySymbol: 'R$',
                  }"
                  :placeholder="'Digite o valor'"
                  v-if="type == 'MONETARY'"
                ></vue-autonumeric>
                <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" v-model="createVal" placeholder="Digite o valor">
                <br>
                <button v-on:click="createSeed">Criar</button>
              </form>
              <div class="resposta" v-show="msgResposta.length > 0">
                <hr>
                <p>{{msgResposta}}<br>Atualizando...</p>
                <hr>
              </div>
            </div>
        </b-tab>
        <b-tab v-if="actualOk">
          <template slot="title">
            <fa :icon="['fas', 'exchange-alt']"></fa>
          </template>
            <div v-if="Object.keys(actualUser.seeds).length > 0" class="titleSelected">Saldo Disponível em: {{actualUser.seeds[fracIdCamp].description}} - {{parseFloat(actualUser.seeds[fracIdCamp].balance).toLocaleString('pt-BR', (actualUser.seeds[fracIdCamp].type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="actualUser.seeds[fracIdCamp].type == 'PROMOTIONAL'"> Pontos</span>
            </div>
            <div id="frac" v-if="Object.keys(actualUser.seeds).length > 0">
              <h2>Transferir Crédito</h2>
              <form action="" v-on:submit.prevent="fracSeed">
                <label for="seed">Crédito:</label>
                <select v-model="fracIdCamp" class="campanha">
                  <option v-for="doc in actualUser.seeds" :value="doc.id" :data-seed="doc.docId">{{doc.description}}</option>
                </select>
                <br>
                <label for="dest">Destino:</label>
                <select v-model="fracIdUser">
                  <option v-for="(merc) in users.filter(usr => usr.id != idUserProp && usr.id != seeds.find(sd => sd.id == fracIdCamp).emitterId)" v-bind:key="merc.id" :value="merc.id">{{merc.username}}</option>
                </select>
                <br>
                <label for="descTrans">Descrição:</label>
                <input name="descTrans" type="text" placeholder="Digite a descrição" v-model="fracDesc">
                <br>
                <label for="valor">Valor:</label>
                <vue-autonumeric v-model="fracValue"
                  :options="{
                    digitGroupSeparator: '.',
                    decimalCharacter: ',',
                    decimalCharacterAlternative: '.',
                    currencySymbol: 'R$',
                    }"
                    :placeholder="'Digite o valor'"
                    v-if="actualUser.seeds[fracIdCamp].type == 'MONETARY'"
                  ></vue-autonumeric>
                <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" placeholder="Digite o valor" v-model="fracValue">
                <br>
                <button type="submit" >Transferir</button>
              </form>
              <div class="resposta" v-show="msgResposta.length > 0">
                <hr>
                <p>{{msgResposta}}<br>Atualizando...</p>
                <hr>
              </div>
            </div>
            <div v-else>
              <h2>Você não possui créditos para transferir</h2>
            </div>
        </b-tab>
        <b-tab v-if="actualOk">
          <template slot="title">
            <fa :icon="['fas', 'wallet']"></fa>
          </template>
            <div v-if="Object.keys(actualUser.seeds).length > 0" class="titleSelected">Saldo Disponível em: {{actualUser.seeds[fracIdCamp].description}} - {{parseFloat(actualUser.seeds[fracIdCamp].balance).toLocaleString('pt-BR', (actualUser.seeds[fracIdCamp].type == 'PROMOTIONAL') ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2})}}<span v-if="actualUser.seeds[fracIdCamp].type == 'PROMOTIONAL'"> Pontos</span>
            </div>
            <div id="cashout" v-if="Object.keys(actualUser.seeds).length > 0">
              <h2>Usar Crédito</h2>
              <select v-model="fracIdCamp">
                <option v-for="doc in actualUser.seeds" :value="doc.id">{{doc.description}}</option>
              </select>
              <br>
              <vue-autonumeric v-model="cashValue"
              :options="{
                digitGroupSeparator: '.',
                decimalCharacter: ',',
                decimalCharacterAlternative: '.',
                currencySymbol: 'R$',
                }"
                :placeholder="'Digite o valor'"
                v-if="actualUser.seeds[fracIdCamp].type == 'MONETARY'"
              ></vue-autonumeric>
              <input v-else name="valor" type="number" step="1" onkeypress="return event.charCode >= 48 && event.charCode <= 57" placeholder="Digite o valor" v-model="cashValue">
              <br>
              <button v-on:click.prevent="cashout">Usar</button>
              <div class="resposta" v-show="msgResposta.length > 0">
                <hr>
                <p>{{msgResposta}}<br>Atualizando...</p>
                <hr>
              </div>
            </div>
            <div v-else>
              <h2>Você não possui créditos para utilizar</h2>
            </div>
        </b-tab>
        <b-tab>
          <template slot="title">
            <fa :icon="['fas', 'user-circle']"></fa>
          </template>
          <div id="user">
            <h2>Trocar usuário</h2>
            <select v-model='idUserProp'>
              <option v-for="(opt) in users" :value='opt.id' v-bind:key='opt.id'>{{opt.username}}</option>
            </select>
          </div>
        </b-tab>
      </b-tabs>
    </div>
    <div id="logs" v-if="showHist">
      <div class="sysout">
        <h2>Histórico geral</h2>
        <hr>
        <div class="wrapperLogs">
          <template v-for="dc in docsH" v-if="docs.length > 0">
            <template v-if="!dc.parentId">
              <span>O usuário <strong>{{users.find(id => id.id == dc.emitterId).username}}</strong> criou a campanha <strong>{{seeds.find(nm => nm.id == dc.seedId).description}}</strong> com um valor de <strong>{{parseFloat(dc.value).toLocaleString('pt-BR', (seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</strong></span><span v-if="seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL'"><strong> Pontos</strong></span><br><br>
            </template>
            <template v-else>
              <template v-if="dc.status == 'USED'">
                <span>O usuário <strong>{{users.find(id => id.id == dc.ownerId).username}}</strong> fez um saque da campanha <strong>{{seeds.find(nm => nm.id == dc.seedId).description}}</strong> com um valor de <strong>{{parseFloat(dc.balance).toLocaleString('pt-BR', (seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</strong></span><span v-if="seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL'"><strong> Pontos</strong></span><br><br>
                <template v-if="dc.ownerId != dc.emitterId">
                  <span>O usuário <strong>{{users.find(id => id.id == dc.ownerId).username}}</strong> recebeu de <strong>{{users.find(id => id.id == dc.emitterId).username}}</strong> da campanha <strong>{{seeds.find(nm => nm.id == dc.seedId).description}}</strong> um valor de <strong>{{parseFloat(dc.value).toLocaleString('pt-BR', (seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</strong></span><span v-if="seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL'"><strong> Pontos</strong></span><br><br>
                </template>
              </template>
              <template v-else-if="dc.parentId != dc.ownerId && dc.emitterId != dc.ownerId">
                <span>O usuário <strong>{{users.find(id => id.id == dc.ownerId).username}}</strong> recebeu de <strong>{{users.find(id => id.id == dc.emitterId).username}}</strong> da campanha <strong>{{seeds.find(nm => nm.id == dc.seedId).description}}</strong> um valor de <strong>{{parseFloat(dc.value).toLocaleString('pt-BR', (seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</strong></span><span v-if="seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL'"><strong> Pontos</strong></span><br><br>
              </template>
              <template v-else-if="docs.find(id => id.id == dc.parentId).status != 'USED'">
                <span>O usuário <strong>{{users.find(id => id.id == dc.emitterId).username}}</strong> enviou para <strong>{{users.find(id => id.id == dc.ownerId).username}}</strong> da campanha <strong>{{seeds.find(nm => nm.id == dc.seedId).description}}</strong> um valor de <strong>{{parseFloat(dc.value).toLocaleString('pt-BR', (seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL' ? {} : {style:'currency', currency: 'BRL', minimumFractionDigits:2, maximumFractionDigits:2}))}}</strong></span><span v-if="seeds.find(nm => nm.id == dc.seedId).type == 'PROMOTIONAL'"><strong> Pontos</strong></span><br><br>
              </template>
            </template>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import VueAutonumeric from 'vue-autonumeric';
  export default {
    name: 'card',
    props: [
      'idUser',
      'usr',
      'pass',
      'showHist'
    ],
    components: {
        VueAutonumeric,
    },
    data: () => ({
      tabIndex: 0,
      token: 'https://sso-dev.yandeh.com.br/auth/realms/YandehDev/protocol/openid-connect/token',
      apiPath: 'https://yh-funds-management-api-dev.devyandeh.com.br/api/v1',
      idUserProp: '',
      seeds: [],
      users: [],
      docs: [],
      docsH: [],
      actualUser: '',
      fracIdCamp: '',
      fracDesc: '',
      fracIdUser: '',
      fracValue: '',
      type: '',
      frac: '',
      createVal: '',
      createName: '',
      resToken: '',
      actualOk: false,
      msgResposta: '',
      logins: {
        "1": "ANONYMOUS",
        "2": "industria1@yh-funds-management-api.com",
        "3": "eduardo.dutra@construe.net.br",
        "4": "danielmiranda@softbox.com.br",
        "5": "robson@softbox.com.br",
        "6": "pedrohenriqueduarte@ivendas.com.br",
        "7": "industria2@yh-funds-management-api.com",
      }
    }),
    watch: {
      idUserProp: async function(val) {
        if (this.users.length > 0 ) {
          await this.getToken()
          this.mountObj();
          this.actualUser = this.users.find(user => user.id == val);
          this.fracIdCamp = (Object.keys(this.actualUser.seeds).length > 0) ? this.actualUser.seeds[Object.keys(this.actualUser.seeds)[0]].id : '--';
          this.fracIdUser = this.users.filter(us => us.id != val)[0].id;
          this.frac = (Object.keys(this.actualUser.seeds).length > 0) ? this.actualUser.seeds[Object.keys(this.actualUser.seeds)[0]].id : '--';
          this.msgResposta = ''
          this.tabIndex = 0;
        }
      }
    },
    methods: {
      reloadAllCards: function() {
        // console.log('call reloadAll')
        this.$root.$emit('reloadCards')
      },
      async getToken() {
        await fetch(this.token, {
          method: 'POST',
          headers: {'Content-type': 'application/x-www-form-urlencoded'},
          body: 'grant_type=password&scope=openid&client_id=yh-funds-management-api&client_secret=e78a874f-d70e-410d-93a9-3656d546ea78&username='+this.logins[this.idUserProp]+'&password='+atob('eWFuZGVoQDIwMTk=')
        })
        .then(res => res.json())
        .then(rep => {
          this.resToken = 'Bearer '+rep.access_token

          // console.log(this.resToken)
        })
        .catch(err => console.log(err))
      },
      async getSaldo(sdId) {
        return await fetch(`${this.apiPath}/users/${sdId}/balances`, {
          method: 'GET',
          headers: {
            'Accept':'application/json',
            'Content-type': 'application/json',
            'Authorization': this.resToken,
          }
        })
        .then(res => res.json())
        .then(rep => {
          return rep.content

          // console.log(this.resToken)
        })
        .catch(err => console.log(err))
      },

      async loadValues(userReload = this.idUser, valueReload = false) {
        await this.getToken()

        await fetch(`${this.apiPath}/users`, {
          method: 'GET',
          headers: {
            'Accept':'application/json',
            'Content-type': 'application/json',
            'Authorization': this.resToken,
          }
        })
        .then(res => res.json())
        .then(usr => {
          this.users = usr.content.sort(function(a,b){
            var x = a.id < b.id ? -1:1
            return x
          })
          this.users.forEach(async (sdB) => {
            sdB['saldo'] = await this.getSaldo(sdB.id)
          })

          console.log('USERS: ',this.users)
        })

        await fetch(`${this.apiPath}/seeds?size=2000`, {
          method: 'GET',
          headers: {
            'Accept':'application/json',
            'Content-type': 'application/json',
            'Authorization': this.resToken,
          }
        })
        .then(res => res.json())
        .then(seed => {
          this.seeds = seed.content.sort(function(a,b){
            var x = a.id < b.id ? -1:1
            return x
          });

          console.log('SEEDS: ',this.seeds)
        })

        await fetch(`${this.apiPath}/documents?size=2000`, {
          method: 'GET',
          headers: {
            'Accept':'application/json',
            'Content-type': 'application/json',
            'Authorization': this.resToken,
          }
        })
        .then(res => res.json())
        .then(docs => {
          this.docs = docs.content.sort(function(a,b){
            var x = a.id < b.id ? -1:1
            return x
          });
          this.docsH = JSON.parse(JSON.stringify(this.docs)).reverse();

          console.log('DOCS: ',this.docs)
        })

        this.idUserProp = userReload;
        if (this.users.length > 0 ) {
          this.mountObj();
          this.actualUser = this.users.find(user => user.id == this.idUserProp);
          this.fracIdCamp = (Object.keys(this.actualUser.seeds).length > 0) ? this.actualUser.seeds[Object.keys(this.actualUser.seeds)[0]].id : '--';
          this.fracIdUser = this.users.filter(us => us.id != this.idUserProp)[0].id;
          this.frac = (Object.keys(this.actualUser.seeds).length > 0) ? this.actualUser.seeds[Object.keys(this.actualUser.seeds)[0]].id : '--';
          window.setTimeout((that = this) => {
            that.msgResposta = ''
            this.tabIndex = 0;
          }, 3000)
          // console.log(this.actualUser)
        }

        this.fracValue = ''
        this.fracDesc = ''
        this.createVal = ''
        this.cashValue = ''
        this.createName = ''
        this.type = ''

        this.mountObj()

        console.log('ActualUser: '+this.actualUser.username ,this.actualUser)

      },
      mountObj(){
        this.users.forEach(us => {
          var stats = false;
          us['seeds'] = {}
          if(us.id == this.idUserProp) {
            this.docs.filter(dx => (dx.ownerId == this.idUserProp || dx.emitterId == this.idUserProp)).forEach(dc => {
              var comp  = new Date(dc.usedAt)
              var dt = comp.toLocaleString('pt-BR')
              if(!us['seeds'][dc.seedId]) {
                us['seeds'][dc.seedId] = {
                  "description": this.seeds.find(sd => sd.id == dc.seedId).description,
                  "id": dc.seedId,
                  "originalDocId": this.seeds.find(id => id.id == dc.seedId).documentRootId,
                  "type": this.seeds.find(sd => sd.id == dc.seedId).type,
                  "value": (!!us.saldo.find(nm => nm.id.seedId == dc.seedId)) ? us.saldo.find(nm => nm.id.seedId == dc.seedId).value : dc.value,
                  "balance": (!!us.saldo.find(nm => nm.id.seedId == dc.seedId)) ? us.saldo.find(nm => nm.id.seedId == dc.seedId).balance : dc.balance,
                  "docId": dc.id,
                  "history": [
                    {
                      "docId": dc.id,
                      "balance": dc.balance,
                      "value": dc.value,
                      "emitterId": dc.emitterId,
                      "ownerId": dc.ownerId,
                      "status": dc.status,
                      "usedAt": !!dc.usedAt ? dt : '-',
                      "created": (!dc.parentId ? true : false),
                      "sender": ((dc.emitterId == this.idUserProp && !!dc.parentId) ? true : false)
                    }
                  ]
                }
              } else {
                us['seeds'][dc.seedId]['history'].push({
                  "docId": dc.id,
                  "balance": dc.balance,
                  "value": dc.value,
                  "status": dc.status,
                  "usedAt": !!dc.usedAt ? dt : '-',
                  "emitterId": dc.emitterId,
                  "ownerId": dc.ownerId,
                  "created": (!dc.parentId ? true : false),
                  "sender": ((dc.emitterId == this.idUserProp && !!dc.parentId) ? true : false)
                })
              }
            })
          }
        })
        this.actualOk = true;
      },
      async init() {
        this.idUserProp = this.idUser;
        await this.loadValues();
      },
      async createSeed(e) {
        e.preventDefault();
        var seed = {
          "description": this.createName,
          "type": this.type,
          "value": Number((this.type == 'PROMOTIONAL' ? parseInt(this.createVal) : this.createVal)),
        }
        await fetch(`${this.apiPath}/seeds`, {
          method: 'POST',
          body: JSON.stringify(seed),
          headers: new Headers({
            'Authorization': this.resToken,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }),
        })
        .then(response => {
          // alert('Gerou Seed!');
          this.status(response,'create')
          console.log('gravou seed ==> ', response)
        })
      },
      async fracSeed(e) {
        // var fracAltera = e.target.querySelector('select.campanha').selectedOptions[0].dataset.seed

        var fracDoc = {
            "fractions": [{
                "username": this.users.find(na => na.id == this.fracIdUser).username,
                "value": Number(this.actualUser.seeds[this.fracIdCamp].type == 'PROMOTIONAL' ? parseInt(this.fracValue) : this.fracValue),
                "description": (!!this.fracDesc ? this.fracDesc : 'Fracionamento de campanha'),
            }]
        }

        await fetch(`${this.apiPath}/seeds/${this.fracIdCamp}/fractionate`, {
          method: 'POST',
          body: JSON.stringify(fracDoc),
          headers: new Headers({
            'Authorization': this.resToken,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }),
        })
        .then(response => {
          this.status(response,'frac')
          // alert('Gerou Document Fração');
          console.log('Fracionou Document ==> ', response)
        })
        .catch(err => console.log(err));
      },
      async cashout(e) {
        e.preventDefault();

        var cashOut = {
          // "seedId": Number(this.fracIdCamp),
          "value": this.cashValue
        }

        await fetch(`${this.apiPath}/seeds/${this.fracIdCamp}/cash-out` , {
          method: 'PUT',
          body: JSON.stringify(cashOut),
          headers: new Headers({
            'Authorization': this.resToken,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }),
        })
        .then(response => {
          // alert('Feito Cashout');
          this.status(response,'cashout')
          console.log('diminuiu valor do document, feito cashout ==> ', response)
        })
        .catch(err => console.log(err));
      },
      async status(res, type){
        let msg = {
          'cashout': 'Feito cashout',
          'create': 'Campanha criada com sucesso!',
          'frac': 'Crédito transferido com sucesso'
        }

        if(res.status == 401) {
          this.msgResposta = 'Você não tem permissão para visualizar este conteúdo'
        }

        if(res.status == 422 || res.status == 400) {
          this.msgResposta = 'Erro inesperado'
        }
        if(res.status == 200 || res.status == 204 || res.status == 201) {
          this.msgResposta = msg[type]
        }
        await this.reloadAllCards();
      },
    },
    mounted () {
      this.$root.$on('reloadCards', async () => {
        // console.log('call reloadTHIScard', this.idUserProp)
        await this.loadValues(this.idUserProp, true);
      })
      this.init();
    }
  }
</script>

